 .advantages {
     padding-top: 70px;
     padding-bottom: 67px;
     &__item {
         display: flex;
         flex-direction: column;
         align-items: center;
         margin-top: 60px;
         max-width: 280px;
         text-align: center;
         margin-right: 68px;
         &:nth-of-type(2) {
             img {
                 animation-name: heartbeat;
                 animation-duration: 1.5s;
                 animation-timing-function: ease;
                 animation-iteration-count: infinite;
                 &:hover {
                     animation-play-state: paused;
                 }
             }
         }
         &:last-of-type {
             margin-right: 0;
         }
         h3 {
             font-weight: bold;
             font-size: 18px;
             line-height: 18px;
             margin-bottom: 0;
             text-align: center;
             color: #0D0D0D;
             margin-top: 35px;
         }
         p {
             font-weight: 300;
             font-size: 14px;
             line-height: 18px;
             text-align: center;
             color: #0D0D0D;
             margin-top: 20px;
             a {
                 color: #C70101;
             }
         }
     }
     &__wrapper {
         display: flex;
         justify-content: space-between;
     }
 }