.container {
    max-width: 945px;
}

@media screen and (max-width: 991px) {
    .container {
        max-width: 100%;
        padding-left: 20px;
        padding-right: 20px;
    }
    .promo {
        padding-left: 20px;
        padding-right: 20px;
    }
    .advantages {
        padding-right: 20px;
        padding-left: 20px;
    }
    .consultation {
        padding-right: 20px;
        padding-left: 20px;
        .title_left {
            font-size: 23px;
            line-height: 33px;
        }
    }
    .carousel__inner,
    .slick-slide img {
        width: calc(100vw - 150px);
    }
    .footer__info {
        width: 370px;
        height: 230px;
        padding-top: 35px;
        padding-bottom: 35px;
        padding-left: 20px;
        padding-right: 20px;
    }
}

@media screen and (max-width: 767px) {
    .header__official {
        display: none;
    }
    .advantages__wrapper {
        flex-direction: column;
    }
    .advantages__item {
        width: 100%;
        margin: auto;
        margin-top: 20px;
        &:last-of-type {
            margin: auto;
        }
    }
    .carousel .slick-prev img,
    .carousel .slick-next img {
        width: 24px;
        height: 40px;
    }
    .catalog-item {
        width: 48%;
    }
    .feed__text {
        margin-left: 40px;
    }
    .feed__item img {
        width: 150px;
        height: 150px;
    }
    .feed__item {
        margin-top: 50px;
        &:nth-child(2) {
            margin-top: 50px;
        }
    }
    .footer__addr {
        font-size: 16px;
        line-height: 16px;
        margin-bottom: 20px;
    }
    .footer__phone {
        font-size: 15px;
        line-height: 15px;
        margin-bottom: 25px;
    }
    .footer__info {
        width: 300px;
        height: 190px;
    }
}

@media screen and (max-width: 550px) {
    .promo__wrapper {
        align-items: flex-start;
        margin-top: 70px;
    }
    .promo__header {
        font-size: 30px;
        line-height: 35px;
    }
    .promo__subheader {
        font-size: 19px;
        line-height: 23px;
        margin-top: 10px;
    }
    .promo__icons {
        margin-top: 40px;
        width: 100%;
        flex-direction: column;
    }
    .button_main {
        margin: auto;
        margin-top: 35px;
        width: 200px;
        height: 42px;
        font-size: 12px;
    }
    .promo__link {
        margin: auto;
        font-size: 13px;
        margin-top: 15px;
    }
    .header__contacts {
        display: none;
    }
    .promo__icon {
        margin: auto;
        margin-bottom: 20px;
        width: 100%;
    }
    .promo {
        padding-bottom: 50px;
    }
    .advantages {
        padding-top: 50px;
        padding-bottom: 50px;
    }
    .consultation {
        padding-top: 50px;
        padding-bottom: 50px;
        .title_left {
            font-size: 18px;
            line-height: 25px;
        }
        .feed-form {
            width: 100%;
            .button_submit {
                font-size: 15px;
            }
        }
    }
    .consultation__descr {
        font-size: 15px;
        line-height: 20px;
    }
    .carousel .slick-next img {
        width: 20px;
        height: 30px;
    }
    .carousel .slick-prev img {
        width: 20px;
        height: 30px;
    }
    .carousel {
        padding-top: 40px;
        padding-bottom: 40px;
    }
    .catalog-item {
        width: 100%;
    }
    .catalog__tabs {
        flex-direction: column;
        margin-top: 35px;
    }
    .catalog__tab {
        margin-bottom: 10px;
    }
    .catalog {
        padding-bottom: 50px;
    }
    .catalog__content_active {
        margin-bottom: 0px;
        margin-top: 30px;
    }
    .feed__item {
        flex-direction: column;
    }
    .feed__item img {
        width: 120px;
        height: 120px;
        margin: auto;
        margin-bottom: 20px;
    }
    .feed .title {
        margin-bottom: 0;
        line-height: 33px;
    }
    .feed__text {
        padding-right: 0;
        margin-left: 0;
    }
    .footer__info {
        width: 230px;
        height: 130px;
        padding-top: 15px;
        padding-bottom: 15px;
        padding-left: 15px;
        padding-right: 15px;
    }
    .footer__addr {
        font-size: 12px;
        line-height: 13px;
        margin-bottom: 15px;
    }
    .footer__phone {
        font-size: 12px;
        line-height: 13px;
        margin-bottom: 15px;
    }
    .footer__social-item {
        margin-right: 15px;
        img {
            width: 40px;
            height: 40px;
        }
    }
    .modal {
        width: calc(100% - 40px);
        padding-left: 20px;
        padding-right: 20px;
        min-height: unset;
    }
    .feed-form {
        width: 100%;
    }
    .modal__close {
        top: 10px;
        right: 10px;
    }
    .modal__subtitle {
        font-size: 15px;
        line-height: 16px;
    }
    .modal__descr {
        font-size: 12px;
        line-height: 13px;
        margin-bottom: 15px;
    }
    .feed-form {
        margin-top: 25px;
    }
    .feed-form input {
        height: 40px;
        font-size: 12px;
        padding: 0 15px;
    }
    .button_submit {
        height: 40px;
        font-size: 12px;
    }
    .button_mini {
        width: 110px;
        height: 38px;
    }
    .footer iframe,
    .footer {
        height: 400px;
    }
}