.feed-form {
    margin-top: 36px;
    display: flex;
    flex-direction: column;
    width: 300px;
    input {
        background: #FFFFFF;
        height: 54px;
        border-radius: 5px;
        margin-bottom: 15px;
        border: none;
        outline: none;
        cursor: pointer;
        color: #4B4B4B;
        font-size: 14px;
        padding: 0 20px;
        font-weight: 300;
        &:last-of-type {
            margin-bottom: 20px;
        }
    }
}