.overlay {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
}

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 382px;
    min-height: 402px;
    background: #F2F2F2;
    padding-top: 35px;
    padding-bottom: 40px;
    padding-left: 40px;
    padding-right: 40px;
    &__close {
        position: absolute;
        top: 0;
        right: -40px;
        font-size: 41px;
        line-height: 9px;
        color: #C70101;
        cursor: pointer;
    }
    &__subtitle {
        font-weight: bold;
        font-size: 18px;
        line-height: 19px;
        text-align: center;
        color: #0D0D0D;
        margin-bottom: 10px;
    }
    &__descr {
        font-weight: 500;
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        color: #0D0D0D;
        margin-bottom: 26px;
    }
    &_mini {
        min-height: 140px;
    }
    .error {
        border: 1px solid red;
    }
    label.error {
        border: none;
        text-align: center;
        margin-bottom: 15px;
    }
}

#consultation,
#order,
#thanks {
    display: none;
}