.carousel {
    padding-top: 80px;
    padding-bottom: 80px;
    &__inner {
        width: 750px;
        margin: 0 auto;
        position: relative;
    }
    .slick-prev,
    .slick-next {
        position: absolute;
        border: none;
        outline: none;
        background-color: white;
        top: 50%;
        transform: translateY(-50%);
        left: -66px;
        img {
            width: 31px;
            height: 50px;
        }
    }
    .slick-next {
        right: -66px;
        left: auto;
    }
}