 @import 'animations';
 .container {
     max-width: 950px;
     margin: 0 auto;
 }
 
 .title {
     font-weight: bold;
     font-size: 30px;
     line-height: 30px;
     text-align: center;
     color: #0D0D0D;
     text-transform: uppercase;
     &_left {
         font-size: 36px;
         text-align: left;
         color: #FFFFFF;
         line-height: 54px;
         margin-bottom: 0;
     }
 }
 
