 .button {
     width: 153px;
     height: 32px;
     background: #C70101;
     border-radius: 5px;
     border: none;
     cursor: pointer;
     color: #FFFFFF;
     font-weight: 500;
     font-size: 12px;
     text-transform: uppercase;
     outline: none !important;
     &_main {
         margin-top: 67px;
         width: 303px;
         height: 54px;
         font-size: 16px;
         font-weight: 400;
     }
     &_submit {
         width: 100%;
         height: 55px;
         outline: none;
         cursor: pointer;
         background: #C70101;
         color: #FFFFFF;
         font-size: 16px;
     }
     &_mini {
         font-size: 14px;
         line-height: 22px;
         text-align: center;
         width: 135px;
         height: 44px;
     }
 }