 .header {
     display: flex;
     justify-content: space-between;
     align-items: center;
     &__logo {
         width: 250px;
         img {
             width: 100%;
         }
     }
     &__official {
         color: #FFFFFF;
         font-weight: 500;
         font-size: 12px;
         text-align: center;
         span {
             display: block;
             font-size: 18px;
         }
     }
     &__phone {
         display: block;
         font-weight: bold;
         font-size: 18px;
         line-height: 18px;
         color: #FFFFFF;
         margin-bottom: 4px;
         &:hover {
             color: #FFFFFF;
             text-decoration: none;
         }
     }
     &__contacts {
         display: flex;
         flex-direction: column;
         align-items: flex-end;
     }
 }