.consultation {
    padding-top: 100px;
    padding-bottom: 108px;
    background: url('../img/bg2.jpg') center (center/cover) no-repeat;
    &__descr {
        font-weight: 300;
        font-size: 20px;
        line-height: 30px;
        color: #FFFFFF;
        margin-top: 8px;
    }
    .error {
        border: 1px solid red;
    }
    label.error {
        color: white;
        border: none;
        text-align: center;
        margin-bottom: 15px;
    }
}