.footer {
    height: 630px;
    position: relative;
    &__info {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 460px;
        height: 270px;
        background-color: #C70101;
        padding-top: 50px;
        padding-bottom: 56px;
        padding-left: 54px;
        padding-right: 54px;
    }
    &__addr {
        font-weight: 500;
        font-size: 20px;
        line-height: 20px;
        text-align: center;
        color: #FFFFFF;
        margin-bottom: 36px;
    }
    &__phone {
        font-weight: bold;
        font-size: 20px;
        line-height: 20px;
        text-align: center;
        color: #FFFFFF;
        display: block;
        margin-bottom: 36px;
        transition: all .3s;
        &:hover {
            color: white;
        }
    }
    &__social {
        align-items: center;
        justify-content: center;
        display: flex;
    }
    &__social-item {
        margin-right: 27px;
        &:last-of-type {
            margin-right: 0;
        }
    }
}