 .catalog {
     padding-bottom: 79px;
     &__tabs {
         margin-top: 46px;
         list-style-type: none;
         display: flex;
         justify-content: center;
         align-items: center;
         margin-bottom: 0;
         padding-left: 0;
     }
     &__tab {
         background: rgba(0, 0, 0, 0.0001);
         border: 2px solid #D0D0D0;
         box-sizing: border-box;
         width: 222px;
         height: 52px;
         font-size: 18px;
         line-height: 52px;
         text-align: center;
         text-transform: uppercase;
         color: #6B6B6B;
         transform: skewX(-25deg);
         margin: 0 5px;
         cursor: pointer;
         transition: 0.5s all;
         &:hover {
             box-shadow: 3px 3px 20px #C70101;
             border: none;
         }
         div {
             transform: skewX(25deg);
         }
         &_active {
             background-color: #C70101;
             color: #FFFFFF;
             border: none;
         }
     }
     &__content {
         margin-top: 57px;
         display: none;
         justify-content: space-between;
         flex-wrap: wrap;
         &_active {
             display: flex;
         }
     }
     &-item {
         width: 303px;
         min-height: 442px;
         border: 2px solid #E8E8E8;
         box-sizing: border-box;
         padding-top: 20px;
         padding-bottom: 20px;
         padding-right: 20px;
         padding-left: 20px;
         overflow: hidden;
         margin-bottom: 20px;
         &__img {
             max-width: 100%;
             height: 200px;
             display: block;
             margin: 0 auto;
         }
         &__subtitle {
             margin-top: 15px;
             font-weight: 700;
             font-size: 16px;
             line-height: 25px;
             text-align: center;
             color: #0D0D0D;
         }
         &__descr {
             margin-top: 15px;
             font-weight: 300;
             font-size: 14px;
             line-height: 22px;
             text-align: center;
             color: #0D0D0D;
         }
         &__link {
             margin-top: 10px;
             font-weight: 500;
             font-size: 14px;
             line-height: 14px;
             text-align: center;
             text-decoration-line: underline;
             color: #C70101;
             display: block;
             &:hover {
                 color: #C70101;
             }
         }
         &__back {
             position: absolute;
             bottom: 0;
             left: 50%;
             transform: translateX(calc(-50% - 19px));
             display: block;
             color: #C70101;
             font-weight: 500;
             font-size: 14px;
             text-decoration-line: underline;
             text-transform: uppercase;
             &:hover {
                 color: #C70101;
             }
         }
         hr {
             margin-top: 17px;
             margin-bottom: 24px;
             background: #E8E8E8;
             border-bottom: none;
         }
         &__footer {
             display: flex;
             justify-content: space-between;
             align-items: center;
         }
         &__old-price {
             font-weight: 300;
             font-size: 14px;
             line-height: 14px;
             text-decoration-line: line-through;
             color: #555555;
         }
         &__price {
             font-weight: bold;
             font-size: 20px;
             line-height: 20px;
             color: #050505;
         }
         &__wrapper {
             display: flex;
             width: calc(200% + 40px);
         }
         &__content {
             width: 50%;
             margin-right: 0px;
             transform: translateX(-100%);
             transition: o.5s all;
             &_active {
                 transform: translateX(0%);
                 margin-right: 20px;
             }
         }
         &__list {
             width: 50%;
             margin-left: 20px;
             transform: translateX(0%);
             padding-right: 20px;
             transition: o.5s all;
             padding-left: 19px;
             margin-bottom: 0;
             list-style-type: none;
             position: relative;
             &_active {
                 transform: translateX(-100%);
                 margin-left: 0px;
             }
             li {
                 font-weight: 300;
                 font-size: 14px;
                 line-height: 21px;
                 color: #555555;
                 margin-bottom: 10px;
                 position: relative;
                 &::before {
                     content: "";
                     position: absolute;
                     width: 5px;
                     height: 5px;
                     background: #C70101;
                     left: -15px;
                     top: 9px;
                 }
             }
             &__back {
                 position: absolute;
                 bottom: 0;
                 left: 50%;
                 transform: translateX(calc(-50% -20px));
                 display: block;
                 font-weight: 500;
                 font-size: 14px;
                 line-height: 14px;
                 text-align: center;
                 text-decoration-line: underline;
                 text-transform: uppercase;
                 color: #C70101;
                 &:hover {
                     color: #C70101;
                 }
             }
         }
     }
 }